import Avatar from "@components/avatar"
import AvatarGroup from "@components/avatar-group"
import { Calendar, MapPin, Video } from "react-feather"
import {
  Col,
  Card,
  CardTitle,
  CardBody,
  CardText,
  CardHeader,
  CardFooter,
  Button,
  Spinner,
  Badge
} from "reactstrap"
import { useDispatch, useSelector } from "react-redux"
import { useTranslation } from "react-i18next"
import illustration from "@src/assets/images/illustration/email.svg"
import { Fragment, useEffect, useMemo, useContext, useState } from "react"
import UILoader from "@components/ui-loader"
import { NoDataDisplay, Loader } from "@src/shared/CommonComponents"
import Moment from "react-moment"
import "moment-timezone"
import "moment/locale/id"
import { putDataWidget, getDataList } from "@src/shared/dynamic_pages/store"

import { Swiper, SwiperSlide } from "swiper/react/swiper-react"
import EventAvailableOutlinedIcon from "@mui/icons-material/EventAvailableOutlined"
import toast from "react-hot-toast"
import { ThemeColors } from "@src/utility/context/ThemeColors"
import { getDataList as getDataListMyActivity } from "@src/views/my-info/store/my_activities"
import InsertInvitationOutlinedIcon from "@mui/icons-material/InsertInvitationOutlined"

const params = {
  // navigation: true
  pagination: {
    clickable: true
  },
  autoplay: {
    delay: 10000,
    disableOnInteraction: false
  }
}

const WidgetTrainingNeedApproval = () => {
  const { t } = useTranslation()
  // const store = useSelector((state) => state[storeName])
  const store = useSelector((state) => state.dynamicStore)
  const dynamicTempState = useSelector((state) => state.dynamic_temp_state)
  const dispatch = useDispatch()

  const { colors } = useContext(ThemeColors)
  const [loading, setLoading] = useState(false)
  const [loadingPostPut, setLoadingPostPut] = useState(false)
  const [loadingDelete, setLoadingDelete] = useState(false)
  const [loadingText, setLoadingText] = useState("Please Wait")

  const storeName = "TrainingEmployee_needApproval"
  const endpointName = "TrainingEmployee/assignment"

  useEffect(() => {
    if (
      !store.pageData[storeName] ||
      (store.pageData[storeName]?.data?.length === 0 &&
        !store.pageData[storeName]?.isLoading)
    ) {
      dispatch(
        getDataList({
          storeName: storeName,
          endpointName: endpointName,
          baseUrl: "hr",
          type: "to_me",
          Id_AttendingState: 3
        })
      )
    }
  }, [])

  const errorToast = (res) => {
    let errorFieldsMsg = ""
    if (res?.payload?.error?.errors) {
      const errorFields = res?.payload?.error?.errors
      for (let errorx in res?.payload?.error?.errors) {
        errorFieldsMsg = `${errorFieldsMsg} - ${errorFields[errorx]}\n`
        // console.log(errorx, errorFields[errorx])
      }
    }
    return toast.error(
      `${res.payload?.error?.message} \n \n ${errorFieldsMsg}`,
      {
        style: {
          padding: "16px",
          color: colors.danger.main,
          border: `1px solid ${colors.danger.main}`
        },
        iconTheme: {
          primary: colors.danger.main
        }
      }
    )
  }

  const putDataToApi = (data) => {
    setLoadingPostPut(true)
    setLoadingText("Confirming Training Request")
    data["storeName"] = storeName
    data["endpointName"] = endpointName
    data["baseUrl"] = "hr"
    data["callback_page"] = window.location.href
    dispatch(putDataWidget(data))
      .then((res) => {
        // console.log(res)

        if (res.meta.requestStatus === "fulfilled") {
          dispatch(
            getDataList({
              storeName: storeName,
              endpointName: endpointName,
              baseUrl: "hr",
              type: "to_me",
              Id_AttendingState: 3
            })
          )
            .then((xxx) => {
              toast.success(
                `${t("Successfully Confirming Training Request")}}`,
                {
                  style: {
                    padding: "16px",
                    color: colors.success.main,
                    border: `1px solid ${colors.success.main}`
                  },
                  iconTheme: {
                    primary: colors.success.main
                  }
                }
              )
            })
            .then((x) => {
              setLoadingPostPut(false)
            })
            .then((xx) => {
              dispatch(
                getDataList({
                  storeName: "TrainingEmployee_task",
                  endpointName: "TrainingEmployee/assignment",
                  baseUrl: "hr",
                  type: "to_me",
                  Id_AttendingState: 1
                })
              )
              setTimeout(() => {
                dispatch(getDataListMyActivity({}))
              }, 8000)
            })
        } else if (res.meta.requestStatus === "rejected") {
          errorToast(res)
        }
      })
      .catch((error) => {
        // console.error(error)
        setLoadingPostPut(false)
        errorToast(error)
      })
  }

  const calendarStrings = {
    lastDay: "[Yesterday]",
    sameDay: "[Today]",
    nextDay: "[Tomorrow]",
    lastWeek: "[last] dddd",
    nextWeek: "dddd",
    sameElse: "L"
  }

  const toUpperCaseFilter = (d) => {
    return d.toUpperCase()
  }

  {
    return store.pageData[storeName]?.isLoading ? (
      <Col lg="12" xs="12">
        <Card style={{ minHeight: "200px" }}>
          <CardHeader>
            <div className="d-flex align-items-center">
              <InsertInvitationOutlinedIcon className="user-timeline-title-icon me-1" />
              <CardTitle tag="h4">{t("Need Approval")}</CardTitle>
            </div>
          </CardHeader>
          <UILoader blocking={true} loader={<Loader />}>
            <CardBody></CardBody>
          </UILoader>
        </Card>
      </Col>
    ) : store.pageData[storeName]?.data?.length > 0 ? (
      <Col lg="12" xs="12">
        <Card>
          <CardHeader>
            <div className="d-flex align-items-center">
              <InsertInvitationOutlinedIcon className="user-timeline-title-icon me-1" />
              <CardTitle tag="h4">{t("Training Need Approval")}</CardTitle>
              {store.pageData[storeName]?.data?.length > 1 && (
                <Badge className="ms-1" tag="div" color="danger" pill>
                  {store.pageData[storeName]?.data?.length}
                </Badge>
              )}
            </div>
          </CardHeader>

          <CardBody className="px-0 pb-0">
            <Swiper {...params}>
              {store.pageData[storeName]?.data.map((row) => {
                return (
                  <SwiperSlide key={`swiper_${row.id}`}>
                    <Card className="card-developer-meetup  cursor-pointer">
                      <div className="meetup-img-wrapper rounded-top text-center">
                        <img
                          src={
                            row?.TrainingData?.Image
                              ? row.TrainingData.Image
                              : illustration
                          }
                          height="170"
                        />
                      </div>
                      <CardBody>
                        <div className="meetup-header d-flex align-items-center">
                          <div className="meetup-day">
                            <h6 className="mb-0">
                              <Moment filter={toUpperCaseFilter} format="ddd">
                                {row?.TrainingData?.StartDate}
                              </Moment>
                            </h6>
                            <h3 className="mb-0">
                              <Moment format="DD">
                                {row?.TrainingData?.StartDate}
                              </Moment>
                            </h3>
                          </div>
                          <div className="my-auto text-truncate">
                            <CardTitle tag="h4" className="mb-25">
                              {row?.TrainingData?.Name}
                            </CardTitle>
                            <CardText className="mb-0">
                              {row?.TrainingData?.Description}
                            </CardText>
                          </div>
                        </div>
                        <div className="d-flex">
                          <Avatar
                            color="light-primary"
                            className="rounded me-1"
                            icon={<Calendar size={18} />}
                          />
                          <div>
                            {/* <h6 className="mb-0">Sat, May 25, 2020</h6>
                    <small>10:AM to 6:PM</small> */}
                            <h6 className="mb-0">
                              {
                                <Moment calendar={calendarStrings}>
                                  {row?.TrainingData?.StartDate}
                                </Moment>
                              }
                            </h6>
                            <small>
                              {
                                <Moment format="[Start at] LT">
                                  {row?.TrainingData?.StartDate}
                                </Moment>
                              }
                            </small>
                          </div>
                        </div>
                        {!row?.TrainingData?.IsOnline ? (
                          <div className="d-flex mt-2">
                            <Avatar
                              color="light-primary"
                              className="rounded me-1"
                              icon={<MapPin size={18} />}
                            />
                            <div>
                              {/* <h6 className="mb-0">Central Park</h6> */}
                              <small>{row?.TrainingData?.Address}</small>
                            </div>
                          </div>
                        ) : (
                          <div className="d-flex mt-2">
                            <Avatar
                              color="light-primary"
                              className="rounded me-1"
                              icon={<Video size={18} />}
                            />
                            <div>
                              <h6 className="mb-0">{t("Online Training")}</h6>
                              <small>
                                {row?.TrainingData?.LinkRef
                                  ? row?.TrainingData?.LinkRef
                                  : "The link will be sent before the event starts"}
                              </small>
                            </div>
                          </div>
                        )}
                      </CardBody>

                      {row?.Employee.map((item) => {
                        return (
                          <CardFooter>
                            <div className="d-flex align-items-center justify-content-between">
                              <div className="d-flex">
                                <Avatar
                                  imgClassName="rounded"
                                  className="me-75"
                                  img={
                                    `${item?.PhotoPath}` ??
                                    require("@src/assets/images/portrait/small/avatar-s-7.jpg")
                                      .default
                                  }
                                  imgHeight="42"
                                  imgWidth="42"
                                />
                                <div className="my-auto">
                                  <h6 className="mb-0">{`${item?.FirstName} ${item?.LastName}`}</h6>
                                  {/* <small>{task.subtitle}</small> */}
                                </div>
                              </div>
                              <div className="d-flex">
                                {!loadingPostPut &&
                                  (!loadingDelete ? (
                                    <Button.Ripple color="flat-secondary me-1">
                                      {t("Decline")}
                                    </Button.Ripple>
                                  ) : (
                                    <Button.Ripple color="flat-secondary me-1">
                                      <Spinner
                                        color="white"
                                        size="sm"
                                        type="grow"
                                      />
                                      <span className="ms-50">
                                        {loadingText}
                                      </span>
                                    </Button.Ripple>
                                  ))}

                                {!loadingPostPut ? (
                                  <Button.Ripple
                                    onClick={(e) => {
                                      e.preventDefault()
                                      putDataToApi({
                                        id: row?.Id_Training,
                                        Id_Employee: item.id
                                      })
                                    }}
                                    color="gradient-primary"
                                  >
                                    {t("Confirm")}
                                  </Button.Ripple>
                                ) : (
                                  <Button.Ripple color="gradient-primary">
                                    <Spinner
                                      color="white"
                                      size="sm"
                                      type="grow"
                                    />
                                    <span className="ms-50">{loadingText}</span>
                                  </Button.Ripple>
                                )}
                              </div>
                            </div>
                          </CardFooter>
                        )
                      })}
                    </Card>
                  </SwiperSlide>
                )
              })}
            </Swiper>
          </CardBody>
        </Card>
      </Col>
    ) : null
  }
}

export default WidgetTrainingNeedApproval
